
import React, {useEffect, useState, useReducer} from 'react';
import { NavigationTree } from '@assets/navigation-tree';
import {View, Provider,ProgressCircle } from '@adobe/react-spectrum';
import FolderIcon from '@react/react-spectrum/Icon/Folder';
import FolderOpenIcon from '@react/react-spectrum/Icon/FolderOpen';
import { cloneDeep } from 'lodash';
import { searchTree, findFolder, isDuplicate } from './utils';
import Constants from './Constants';
import styles from './styles/TreeNav.module.css';

const CCFolderIconJSX = <FolderIcon />
const CCFolderOpenIconJSX = <FolderOpenIcon />

let ORIGIN = window.location.origin;

export const TreeNav = (props) => {

  const [cache, setCache] = useState([]);
  const [assetsTree, setAssetsTree] = useState([]);
  const [isLoading, setIsLoading] = useState(false);
  
  // Step 1: Fetch Accounts
  useEffect(() => {
    
    const requestAccounts = async () => {
      setIsLoading(true);
      let api = `${ORIGIN}/services/fetchAccounts`;
      const accountsResponse = await fetch(api, {
        method: 'GET',
        headers: { 'Content-Type': 'application/json' }
      }).then(accountsResponse => accountsResponse.json());

      let auxTree = accountsResponse.reduce((acc, cur) => {
        acc.push({
          id: cur['id'],
          name: cur['display_name'],
          children: [],
          icon: CCFolderOpenIconJSX,
          team_count: cur['team_count']
        });

        return acc;
      }, [], "");

      setAssetsTree(auxTree);
      setIsLoading(false);
    }

    requestAccounts();

  }, []);
  //--end step

  // Step 2: Fetch Teams
  const requestTeams = async (itemId) => {
    setIsLoading(true);
    let api = `${ORIGIN}/services/fetchTeams.${itemId}.html`;

    const teamsResponse = await fetch(api, {
      method: 'GET',
      headers: { 'Content-Type': 'application/json' }
    }).then(res => res.json());

    let auxTree = cloneDeep(assetsTree);
    let assetsArr = [];

    auxTree.every((node) => {
      let _node = searchTree(node, itemId);

      if (_node && _node.node) {
        
        if (teamsResponse.hasOwnProperty('code') && teamsResponse.code === 500) {
          console.log('>> Error: Something went wrong with teams request');
          return false;//there was an error
        }

        teamsResponse.forEach((child) => {
          if(!isDuplicate(_node.node.children, child['id'])) {
            _node.node.children.push({
              id: child['id'],
              name: child['name'],
              children: [],
              icon: CCFolderOpenIconJSX,
              project_count: child['project_count'],
              type: child['type'],
            });
          }
          
          assetsArr.push({
            id: child['id'],
            name: child['name'],
            type: child['type'],
            selected: false
          })
          setIsLoading(false);
        });
        return false;//break loop when found
      }

      return true;
    });

    setAssetsTree(auxTree);
    props.setAssets([]);
  }
  //--end step

  // Step 3: Fetch Projects  
  const requestProjects = async (itemId) => {
    setIsLoading(true);
    let api = `${ORIGIN}/services/fetchProjects.${itemId}.html`;

    const projectsResponse = await fetch(api, {
      method: 'GET',
      headers: { 'Content-Type': 'application/json' }
    }).then(res => res.json());

    let auxTree = cloneDeep(assetsTree);
    let assetsArr = [];
    
    auxTree.every((node) => {
      let _node = searchTree(node, itemId);

      if (_node && _node.node) {
        projectsResponse.forEach((child) => {   
         
          if(!isDuplicate(_node.node.children, child['id'])) {
            _node.node.children.push({
              id: child['id'],
              name: child['name'],
              type: child['_type'],
              children: [],
              file_count: child['file_count'],
              root_asset_id: child['root_asset_id'],
              folder_count: child['folder_count'],
              icon: CCFolderOpenIconJSX
            });
          }

          assetsArr.push({
            id: child['root_asset_id'], //child['id'],Substitute id for root_asset_id for project folder selection [export use-case]
            root_asset_id: child['root_asset_id'],
            name: child['name'],
            type: child['_type'],
            selected: false
          });
          setIsLoading(false);
          
        });
        return false;//break loop when found
      }

      return true;
    });

    setAssetsTree(auxTree);

    if(Constants.EXPORT_ACTION === props.action) {
      props.setAssets(assetsArr);
    } else {
      props.setAssets([]);
    }
    
  }
  //--end step

  // Step 4: Fetch Assets
  const requestAssets = async (assetId) => {
    setIsLoading(true);
    let api = `${ORIGIN}/services/fetchAssets.${assetId}.html`;

    const assetsResponse = await fetch(api, {
      method: 'GET',
      headers: { 'Content-Type': 'application/json' }
    }).then(res => res.json());

    let assetsArr = [];
    let maxSize = assetsResponse.asset_size_limit;

    assetsResponse.assets.forEach((asset) => {
      
      if(Constants.IMPORT_ACTION === props.action && Constants.FOLDER_ASSET_TYPE != asset.type) {
        assetsArr.push({
          id: asset['id'],
          root_asset_id: asset['id'],
          name: asset['name'],
          children: [],
          type: asset['type'],
          selected: false,
          thumb: asset['thumb'],
          original: asset['original'],
          mimetype: asset['mimetype'],
          filesize: asset['filesize'],
          maxSize: maxSize
        });
      }

      if(Constants.EXPORT_ACTION === props.action && Constants.FOLDER_ASSET_TYPE === asset.type) {
        assetsArr.push({
          id: asset['id'],
          root_asset_id: asset['id'],
          name: asset['name'],
          children: [],
          type: asset['type'],
          selected: false
        });
      }
     
    });
    
    props.setAssets(assetsArr);

    let auxTree = cloneDeep(assetsTree);
    auxTree.every((node) => {

      let _node = searchTree(node, assetId);
      
      if (_node && _node.node) {
        assetsResponse.assets.forEach((asset) => {
          if(Constants.FOLDER_ASSET_TYPE === asset.type) {
            
            if(!isDuplicate(_node.node.children, asset['id'])) {
              _node.node.children.push({
                id: asset['id'],
                root_asset_id: asset['id'],
                name: asset['name'],
                children: [],
                icon: CCFolderOpenIconJSX,
                type: asset['type']
              });
            }
          }
        });
        setIsLoading(false);

        return false;//break loop when found
      }

      return true;
    });
    setAssetsTree(auxTree);
  }
  //--end step


  const navigateToAsset = (item) => {
    const itemId = item.id;
    if (item.team_count > 0) {
      requestTeams(itemId);
    } else if (item.project_count > 0) {
      requestProjects(itemId);
    } else if (item.root_asset_id) {
      requestAssets(item.root_asset_id)
    }
  };

  const itemToggled = (item) => {
    //console.log('>> itemToggled');
  };

  return (
      <Provider >
          <View height="100vh" position="relative" paddingLeft="15px">
              <ProgressCircle aria-label="Loading…" isIndeterminate size='S' UNSAFE_className={styles.LoadingIndicator} isHidden={!isLoading}/>
              <NavigationTree className="spectrum--medium" tree={assetsTree} onToggleItem={itemToggled} onSelectionChange={navigateToAsset} height="100vh"/>
          </View>
      </Provider>
  );
};

export default TreeNav;