import "regenerator-runtime/runtime";
import React from 'react';
import ReactDOM,{hydrate} from 'react-dom';
import App from './App';

window.frameio = {};

window.frameio.renderExportDialog = function(){
  let root = document.getElementById('frame-io-export-dialog');
  if(root) {
    ReactDOM.render(<App action={root.dataset.action} aemSelections={root.dataset.selections}/>, root);
  }
}

window.frameio.renderImportDialog = function(){
  let root = document.getElementById('frame-io-import-dialog');
  if(root) {
    ReactDOM.render(<App action={root.dataset.action} aemSelections={root.dataset.selections}/>, root);
  }
}

