/**
 * searchTree
 * @param {*} node 
 * @param {*} targetId 
 * @returns 
 */
function searchTree(node, targetId){


  if(node.id == targetId || node.root_asset_id == targetId){
       return {
        node: node
       };
  }else if (node.children != null){
       var i;
       var result = null;
       for(i=0; result == null && i < node.children.length; i++){
            result = searchTree(node.children[i], targetId);
       }
       return  result;
  }
  return null;
}

//@TODO: include check for 'version_stack'
function fileTypeGenerator (mimetype, thumb, fileType) {
     if (fileType === 'folder') {
          return 'folder';
     } else if ((mimetype === null || mimetype === undefined) && thumb) {
          return 'image';
     } else if ((mimetype === null || mimetype === undefined) && !thumb) {
          return 'file';
     } else if (mimetype.includes('image') || mimetype.includes('video') || mimetype.includes('audio')) {
          return 'image';
     } else {
          return 'folder';
     }
}

function fileSizeConverter(kilaBytes, si=true, decimalPlaces=1) {
     const thresh = si ? 1000 : 1024;
   
     if (Math.abs(kilaBytes) < thresh) {
       return kilaBytes + ' B';
     }
   
     const units = si 
       ? ['MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB'] 
       : ['MiB', 'GiB', 'TiB', 'PiB', 'EiB', 'ZiB', 'YiB'];
     let u = -1;
     const r = 10**decimalPlaces;
   
     do {
       kilaBytes /= thresh;
       ++u;
     } while (Math.round(Math.abs(kilaBytes) * r) / r >= thresh && u < units.length - 1);
   
   
     return kilaBytes.toFixed(decimalPlaces) + ' ' + units[u];
}


function isDuplicate(list, id) {
     let duplicate = false;
     list.forEach((item)=>{
          if(item.id === id){
               duplicate = true;
          }
     });
     return duplicate;
}

export { searchTree, fileTypeGenerator, fileSizeConverter, isDuplicate }; 