import React, {useEffect, useState, useReducer } from 'react';
import {defaultTheme, Provider, Flex, View, Grid, Image, Checkbox, IllustratedMessage, Heading, Content, Text, ActionButton, Badge } from '@adobe/react-spectrum';
import { Asset } from '@react/react-spectrum/Asset';
import FolderIcon from '@react/react-spectrum/Icon/Folder';
import FolderOpenIcon from '@react/react-spectrum/Icon/FolderOpen';
import EmptyFolder from './EmptyFolder';
import { cloneDeep } from 'lodash';
import { ProgressCircle } from '@adobe/react-spectrum';
import styles from './styles/SelectorBrowse.module.css';
import UploadToCloud from '@spectrum-icons/workflow/UploadToCloud';
import BoxImport from '@spectrum-icons/workflow/BoxImport';
import Constants from './Constants';
import { fileSizeConverter } from './utils';

const CCFolderIconJSX = <FolderIcon />
const CCFolderOpenIconJSX = <FolderOpenIcon />

let ORIGIN = window.location.origin;


//===== EmptyScreen ========================================== 

function EmptyScreen() {
  
  return (
    <IllustratedMessage UNSAFE_className="StatusScreenIllustratedMessage" >
      <EmptyFolder />
      <Heading UNSAFE_className={styles.StatusScreenHeading}>This folder is empty</Heading>
      <Content></Content>
    </IllustratedMessage>
  );
}


//===== GridItem ========================================== 

function GridItem(props) {
  let auxAssets = cloneDeep(props.assets);
  const maxSize = parseInt(props.asset.maxSize);
  const maxSizeConverted = fileSizeConverter(maxSize);
  const maxSizeBytes = maxSize * 1000;
  let exceedsMaxSize = (props.asset.filesize > maxSizeBytes);
  let exceedsMaxSizeStyle = (exceedsMaxSize) ? styles.Disabled : '';
  
  const checkBoxStyle = {
    display: 'flex', 
    flexDirection: 'column', 
    alignItems: 'center', 
    justifyContent:'center', 
    width: '42px', 
    height: '42px', 
    margin:'18px 0 0 18px', 
    padding:'0px',
    borderRadius:'5px', 
    background:'rgba(245,245,245,0.9)',
    zIndex:'500'
  };

  const onClick = () => {
    if(exceedsMaxSize) {
      return false;
    }
    
    let _selected = (!props.asset.selected) ? true : false;
    
    auxAssets.forEach((_asset) => {
        if(_asset.id == props.asset.id) {
          Object.assign(_asset,{selected: _selected});
          if(_selected){
            props.setSelectedAsset(_asset);
          }else {
            props.setSelectedAsset(null);
          }
        }else {
          Object.assign(_asset,{selected: false});
        }
    });
    props.setAssets(auxAssets);
  }


  return (
    <div data-filesize={props.asset.filesize} className={`${styles.GridItem} ${props.asset.selected ? styles.selected : ''} ${exceedsMaxSizeStyle}`} onClick={onClick}>
      
      <View position="relative" UNSAFE_className={styles.GridItemBody} borderRadius="medium">
        
        <Flex position="relative" UNSAFE_className={styles.CardPreview} >
          {
            (Constants.EXPORT_ACTION === props.action) ? 
            <Asset type="folder" UNSAFE_className="spectrum-Asset-folder"/> :
            <Image src={props.asset.thumb} UNSAFE_className="spectrum-Asset-thumbnail"/>
          }

          <View position="absolute" UNSAFE_className={styles.CardActions} >
            <Checkbox UNSAFE_style={checkBoxStyle} UNSAFE_className={`checkbox ${props.asset.selected ? 'selected' : ''}`} isSelected={props.asset.selected} aria-label="checkbox" onChange={onClick}/>
        </View>
        </Flex>
        
      </View>
      <View UNSAFE_className={styles.GridItemTitle} paddingTop="size-200">
        {props.asset.name} 
        { exceedsMaxSize ? 
        <Badge variant="neutral" UNSAFE_className={styles.ExceedsMaxSizeBadge}>Exceeds Max Size of {maxSizeConverted}</Badge> :'' 
        }
      </View>
    </div>
  )
}


//===== GridView ========================================== 

function GridView(props) {
  return (
    <Flex direction="row" gap="size-300" wrap UNSAFE_className={styles.GridView}>
      {props.assets.map((asset, index) =>
        <GridItem key={index} action={props.action} asset={asset} assets={props.assets} setAssets={props.setAssets} setSelectedAsset={props.setSelectedAsset} />
      )}
    </Flex>
  );
}


//===== SelectorBrowse ========================================== 

export const SelectorBrowse = (props) => {
  
    const [viewId, setViewId] = useState();
    const [selectedAsset, setSelectedAsset] = useState();
    const [isLoading, setIsLoading] = useState(false);
  
    const getView = () => {
      if(viewId == "Grid") {
        return <GridView action={props.action} assets={props.assets} setAssets={props.setAssets} setSelectedAsset={setSelectedAsset} /> 
      } else {
        return <EmptyScreen />
      }
    }

    const pressHandler = (e) => {
      if(Constants.EXPORT_ACTION == props.action) {
        exportToFrameIO();
      }

      if(Constants.IMPORT_ACTION == props.action) {
        importToAEM();
      }
      
    }

    const importToAEM = () => {
      let aemPath =  props.aemSelections;
      let csrfToken = '';

      //retrieve csrf token
      fetch(Constants.CSRF_TOKEN_URI)
      .then(data => {
        return data.json();
      }).then(csrf => {

        csrfToken = csrf.token;

        //create form data
        const formData = new FormData();
        formData.append(Constants.CSRF_TOKEN_KEY, csrfToken);
        formData.append(Constants.ASSET_ID_KEY, selectedAsset.id);
        formData.append("name", selectedAsset.name);
        formData.append("path", aemPath);
        formData.append("mimetype", selectedAsset.mimetype);
        formData.append("original", selectedAsset.original);
 
      
        const options = {
          method: 'POST',
          body: formData
        };
    
        const url = `${ORIGIN}/services/importAssetsInToAEM`;

        // Send POST request
        let customEvent;
        setIsLoading(true);
        fetch(url, options)
          .then((res) => {
            let json = res.json();
            return res.ok ? json : json.then(Promise.reject.bind(Promise));
          })
          .then((res) => {
            if (res.result === 'success') {
              return customEvent = new CustomEvent('frame-io-dialog-success', {
                detail: {
                  actionType: 'Import',
                }
              });
            } else {
              return Promise.reject('a post error');
            }
          })
          .catch((err) => {
            customEvent = new CustomEvent('frame-io-dialog-error', { 
              detail: { 
                err: err.result, 
                actionType: 'import failed - ' 
              } 
            })
          })
          .finally(() => {
            window.dispatchEvent(customEvent);
            setIsLoading(false);
          });
      });
    }

    const exportToFrameIO = () => {
      let csrfToken = '';

      //retrieve csrf token
      fetch(Constants.CSRF_TOKEN_URI)
      .then(data => {
        return data.json();
      }).then(csrf => {

        csrfToken = csrf.token;

        let decoded = decodeURIComponent(props.aemSelections);
        let selections = JSON.parse(decoded);

        //create form data
        const formData = new FormData();
        formData.append(Constants.CSRF_TOKEN_KEY, csrfToken);
        formData.append(Constants.ASSETS_KEY, selections[0]);
        formData.append(Constants.ASSET_ID_KEY, selectedAsset.id);
      
        const options = {
          method: 'POST',
          body: formData
        };
    
        const url = `${ORIGIN}/services/pushAssetsToFrame`;
        
        let customEvent;
        setIsLoading(true);
        fetch(url, options)
          .then((res) => {
            let json = res.json();
            return res.ok ? json : json.then(Promise.reject.bind(Promise));
          })
          .then((res) => {
            if (res.result === 'success') {
              return customEvent = new CustomEvent('frame-io-dialog-success', {
                detail: {
                  actionType: 'Export',
                }
              });
            } else {
              return Promise.reject('a post error');
            }
          })
          .catch((err) => {
            customEvent = new CustomEvent('frame-io-dialog-error', { 
              detail: { 
                err: err.result, 
                actionType: 'export failed - ' 
              } 
            })
          })
          .finally(() => {
            window.dispatchEvent(customEvent);
            setIsLoading(false);
          });
      });
    }
    
    useEffect(()=>{
      if(props.hasOwnProperty(Constants.ASSETS_KEY) && props.assets.length > 0){
        setViewId(Constants.GRID_VIEW_ID);
      }else{
        setViewId(Constants.EMPTY_SCREEN_VIEW_ID);
      }
    },[props])

    
    return (
        <Provider theme={defaultTheme}>
            <Grid UNSAFE_className={styles.Header} columns={['1fr', '1fr']} >
              <Flex direction="row" alignItems="center">
                  <Heading UNSAFE_className={styles.HeaderHeading}>All Assets</Heading>
              </Flex>
              <Flex direction="row" alignItems="center" justifyContent="end">
                <ProgressCircle aria-label="Loading…" UNSAFE_className={styles.LoadingIndicator} isIndeterminate size='S' isHidden={!isLoading}/>
                <ActionButton isDisabled={!selectedAsset} onPress={pressHandler} UNSAFE_className={ `${styles.ActionButton} ${selectedAsset && styles.ActionButtonAssetSelected}` }>
                  {(props.action === Constants.IMPORT_ACTION) ? <BoxImport /> : <UploadToCloud/> }
                  {(props.action === Constants.IMPORT_ACTION) ? <Text>Import</Text> : <Text>Export</Text> }
                </ActionButton>
              </Flex>
            </Grid>
            <View minHeight="size-800" UNSAFE_className={styles.SelectorBrowse}>
              { getView() }
            </View>
        </Provider>
    );
};

export default SelectorBrowse;