import React, {useEffect, useState, useReducer} from 'react';
import { Flex, View, defaultTheme, Provider, Heading} from '@adobe/react-spectrum';
import { TreeNav } from './TreeNav';
import { SelectorBrowse } from './SelectorBrowse';
import './App.css';

function App(props) {
   
    const [assets, setAssets] = useState([]);

    return (
        <Provider theme={defaultTheme} colorScheme="light" UNSAFE_className="provider-wrapper">
            <Flex direction="row">
                <View width="30%" height="100vh" UNSAFE_className="tree-nav-view">
                    <TreeNav width="350px" action={props.action} aemSelections={props.aemSelections} setAssets={setAssets} />
                </View>
            
                <View width="100%" height="100vh" UNSAFE_className="selector-browse-view">
                    <SelectorBrowse action={props.action} aemSelections={props.aemSelections} assets={assets} setAssets={setAssets}/>
                </View>
            </Flex>
        </Provider>
    );

}

export default App;